import { ComponentProps } from 'react';
import { twMerge } from 'tailwind-merge';

const BUTTON_STYLE = {
  white:
    'bg-white shadow-[0px_1px_1px_0px_#DDE2E9] text-[#42464E]  font-semibold border-[1px] border-[#DDE2E9]',
  black: 'text-white font-semibold bg-[#0C0D0E]',
} as const;

export type ButtonProps = ComponentProps<'button'> & {
  theme?: keyof typeof BUTTON_STYLE;
};

const Button = (props: ButtonProps) => {
  const { className, theme = 'white', ...rest } = props;
  return (
    <button
      type="button"
      style={{ letterSpacing: 0 }}
      className={twMerge(
        'rounded-md h-10 px-6 text-[16px] font-[600] leading-[28px] flex justify-center items-center hover:cursor-pointer transition ease-in-out delay-30 hover:-translate-y-[1px] font-inter',
        className,
        BUTTON_STYLE[theme],
      )}
      {...rest}
    />
  );
};

export { Button };
