import {
  type SpringConfig,
  animated,
  useSpring,
  config,
  UseTransitionProps,
} from 'react-spring';
import { type FC, type ReactNode, useEffect } from 'react';

export const TRANSITION_CONFIG = {
  from: {
    transform: 'translateY(20px)',
    opacity: 0.2,
    scale: 0.98,
  },
  enter: {
    transform: 'translateY(0px)',
    opacity: 1,
    scale: 1,
    config(key: string) {
      if (key === 'opacity') {
        return config.default;
      }
      return config.wobbly;
    },
  },
  leave: {
    opacity: 0,
    scale: 0.98,
    config: config.default,
  },
} satisfies UseTransitionProps;

type FadeInContainerProps = {
  children: ReactNode;
  className?: string;
  delay?: number;
  config?: SpringConfig;
};

export const FadeInContainer: FC<FadeInContainerProps> = props => {
  const { className, children, delay = 0, config: customConfig } = props;

  const [fade, api] = useSpring(() => TRANSITION_CONFIG.from, []);

  useEffect(() => {
    setTimeout(() => {
      api.start({
        ...TRANSITION_CONFIG.enter,
        config: customConfig || TRANSITION_CONFIG.enter.config,
      });
    }, delay);
    return () => {
      api.start({
        ...TRANSITION_CONFIG.leave,
        config: customConfig,
      });
    };
  }, []);

  return (
    <animated.div className={className} style={fade}>
      {children}
    </animated.div>
  );
};

FadeInContainer.displayName = 'FadeInContainer';
