import { useLayoutEffect, useState } from "react";

const isBrowser: boolean = typeof window === 'object';

function isPc() {
  const userAgentInfo = isBrowser ? navigator.userAgent : '';
  const agent = [
    'Android',
    'iPhone',
    'SymbianOS',
    'Windows Phone',
    'iPad',
    'iPod',
  ];
  let flag = true;
  for (const item of agent) {
    if (userAgentInfo.indexOf(item ?? '') > 0) {
      flag = false;
      break;
    }
  }
  return flag;
}

export function useIsMobile(): boolean {
  const [state, setState] = useState(() => !isPc());
  useLayoutEffect(() => {
    let mounted = true;
    const mobile = window?.matchMedia?.('(max-width: 768px)');
    const onChange = () => {
      if (!mounted) {
        return;
      }
      setState(mobile.matches);
    };
    mobile?.addEventListener?.('change', onChange);
    setState(mobile.matches);
    return () => {
      mounted = false;
      mobile?.removeEventListener?.('change', onChange);
    };
  }, []);
  return state;
}